html,
body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-size: 14px;
  font-family: "Open Sans", Tahoma, sans-serif;
  color: #262c37;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  letter-spacing: 0;
  overflow: hidden;
  width: 100%;
  height: 100%; }

img {
  outline: none; }

#___gatsby {
  width: 100%;
  height: 100%; }
  #___gatsby > div:first-child {
    width: 100%;
    height: 100%; }
